import React from "react";
import CashoutButton from "../containers/CashoutButton";

const QuestionListItem = ({ question, id }) => {
  return (
    <li key={id} className="questionLI">
      <p className="listQuestion">{question.text}</p>
      <div className="listMeta">
        Answers: <b>{question.maxAnswers}</b> | Bounty:{" "}
        <b>{question.bounty} ETH</b>
        <div>{question.answer ? "Answer: " + question.answer : ""}</div>
        <div>{question.myAnswer ? "My Answer: " + question.myAnswer : ""}</div>
        <CashoutButton question={question} />
      </div>
      <hr />
    </li>
  );
};

export default QuestionListItem;



