import React from "react";
import { Button } from "reactstrap";

const QuestionMain = ({ active, eth, answerQuestion }) => {
  return active && active.id ? (
    <div>
      <h1>{active.text}</h1>
      <div>
        Answers: {active.totalAnswers}/{active.maxAnswers}
      </div>
      <div>Bounty: {active.bounty ? active.bounty + " ETH" : ""}</div>

      <div className="pt-3">
        <Button
          className="btnAnswers"
          size="lg"
          color="success"
          onClick={() => answerQuestion(eth, active.id, 0)}
        >
          {active.answer1}
        </Button>{" "}
        <Button
          className="btnAnswers"
          size="lg"
          color="success"
          onClick={() => answerQuestion(eth, active.id, 1)}
        >
          {active.answer2}
        </Button>{" "}
      </div>
    </div>
  ) : (
    ""
  );
};

export default QuestionMain;



